// src/SCSS/components/logement.scss

.property-detail {
  width: 80vw;
  max-width: 1240px;
  margin: auto;
  padding: 20px;
  font-family: 'Montserrat', sans-serif;
}
  
.slideshow {
  margin-bottom: 20px; 
}
  
  // Logement informations / titre, localisation, tags 
.property-main-info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.logement-info {
  flex: 1;
  text-align: left;
}

.property-title {
  font-size: 35px;
  margin-bottom: 10px;
  color: #FF6060; 
}

.location {
  font-size: 18px;
  color: black;
  margin-bottom: 20px;
}

.tag {
  display: inline-block;
  background-color: #FF6060; 
  color: white;
  padding: 5px 30px;
  margin-right: 10px;
  border-radius: 12px;
  font-size: 14px;
  font-weight: bold;
  border: 1px solid #FF6060; 
}

// Informations de l'hôte / Images, Nom et Notes
.logement-host {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 20px; 
  margin-top: 20px;
}

.host-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 10px;
}

.host-image {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid #FF6060;
  margin-bottom: 10px;
  margin-left: 12px;
  object-fit: cover;
}

.host-name {
  font-size: 20px;
  font-weight:500;
  color: #FF6060;
  text-align: right; 
  max-width: 100px; 
  white-space: normal; 
}

// Etoiles pour les notes
.logement-rating .star {
  font-size: 20px;
  margin-left: 10px;
}

.logement-rating .filled {
  color: #FF6060; 
}

.logement-rating .empty {
  color: #e3e3e3; 
}

// Description et Equipment de la fiche de logement
.description-equipments {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1240px;
}

.property-description, .property-equipments {
  width: calc(50% - 45px);
}

.property-equipments ul, .property-description p{
  padding-top: 10px;
  padding-left: 20px;
}

.property-equipments li {
  list-style-type: none;
}




